@import url(https://fonts.googleapis.com/css2?family=Stalinist+One&display=swap);
.App {
    width: 100%;
    height: 100%;
}
.Background {
    background: radial-gradient(50% 50% at 50% 50%, #CB0202 0%, #8B2C2C 100%);
    height: 56px;
    position: -webkit-sticky;
    position: sticky;
    -webkit-align-content: center;
            align-content: center;
    color: #FFF500;
    border: 4px solid #7E7E7E;
    box-sizing: border-box;
    border-radius: 8px 8px 0px 0px;
    height: 100%;
}
header {
    background: radial-gradient(50% 50% at 50% 50%, #CB0202 0%, #8B2C2C 100%);
    position: -webkit-sticky;
    position: sticky;
    color: #FFF500;
    border: 4px solid #7E7E7E;
    box-sizing: border-box;
    border-radius: 0px 0px 8px 8px;
}

a.navbar-brand {
    white-space: normal;
    text-align: center;
    word-break: break-all;
}

html {
    font-size: 14px;
}

@media (min-width: 768px) {
    html {
        font-size: 16px;
    }
}

.box-shadow {
    box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05);
}
.Container {
    background: radial-gradient(50% 50% at 50% 50%, #CB0202 0%, #8B2C2C 100%);
    color: #FFF500;
    border: 4px solid #7E7E7E;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 8px;
}
.Button {
    height: 36px;
    background: radial-gradient(50% 50% at 50% 50%, #CB0202 0%, #8B2C2C 100%);
    color: #FFF500;
    border: 4px solid #7E7E7E;
    box-sizing: border-box;
    border-radius: 4px;
    height: 48px;
}

.Button :hover {
    background: radial-gradient(50% 50% at 50% 50%, #CB0202 40%, #8B2C2C 100%);
}

.Button :focus-visible {
    outline: 4px dashed darkorange;
}
.TextBox {
    height: 48px;
    background: radial-gradient(50% 50% at 50% 50%, #CB0202 0%, #8B2C2C 100%);
    color: #FFF500;
    border: 4px solid #7E7E7E;
    box-sizing: border-box;
    border-radius: 4px;
    width: 500px;
}

.TextBox :focus-visible {
    outline: 4px dashed darkorange;
}

.TextBox :focus {
    outline: 2px solid black;
}
.Login {
    width: 100%;
    height: 100%;
}

.Container {
    position: absolute;
    width: 600px;
    height: 400px;
    left: calc(50% - 600px/2 - 91px);
    top: calc(50% - 400px/2 + 6px);
    -webkit-align-content: stretch;
            align-content: stretch;
}

.spacer {
    height: 16px;
}
body {
    font-family: 'Stalinist One';
    margin-top: 0;
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: url("https://bloody-disgusting.com/wp-content/uploads/2016/01/BD16_Asylum_9.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position-x: center;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    height: 100%;
}

html,
body {
    margin: 0;
    height: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
